.resetPasswordContainer {
    width: 100vw;
    height: 100vh;
    max-width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(231, 231, 231);
    position: fixed;
    top: 0%;
    left: 0;
}
.circleClipResetPw{
    width: 100vw;
    height: 100vh;
    position: absolute;
    -webkit-clip-path: circle(50% at 50% 20%);
    clip-path: circle(70% at 100% 20%);
    background-color: blue;
}
.resetPasswordBox {
    z-index: 2;
    background-color: rgba(255, 255, 255, 0.436);
    backdrop-filter: blur(200px);
    width: 25vw;
    height: 50vh;
    box-sizing: border-box;
    overflow: hidden;
    border-radius: 4px;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
}

.resetPasswordHeading {
    text-align: center;
    color: rgb(255, 255, 255);
    font: 400 1.3vmax "Roboto";
    padding: 1.3vmax;
    border-bottom: 1px solid rgba(255, 255, 255, 0.884);
    width: 50%;
    margin: auto;
}

.resetPasswordForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    padding: 2vmax;
    justify-content: space-evenly;
    height: 70%;
    transition: all 0.5s;
}

.resetPasswordForm>div {
    display: flex;
    width: 100%;
    align-items: center;
}

.resetPasswordForm>div>input {
    padding: 1vmax 4vmax;
    padding-right: 1vmax;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.267);
    border-radius: 4px;
    font: 300 0.9vmax cursive;
    outline: none;
}

.resetPasswordForm>div>svg {
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.6vmax;
    color: rgba(0, 0, 0, 0.623);
}

.resetPasswordBtn {
    border: none;
    background-color: #2121c296;
    color: white;
    font: 300 0.9vmax "Roboto";
    width: 100%;
    padding: 0.8vmax;
    cursor: pointer;
    transition: all 0.5s;
    border-radius: 4px;
    outline: none;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.219);
}

.resetPasswordBtn:hover {
    background-color: #0c0c7696;
}

@media screen and (max-width: 1050px) {
    .resetPasswordBox {
        width: 30vw;
        height: 50vh;
    }
}
@media screen and (max-width: 850px) {
    .resetPasswordContainer {
        background-color: white;
    }
    /* .circleClipResetPw{
        height: 0;
        width: 0;
    } */
    .resetPasswordBox {
        width: 70vw;
        height: 50vh;
    }

    .resetPasswordForm {
        padding: 5vmax;
    }

    .resetPasswordForm>div>input {
        padding: 2.5vmax 5vmax;
        font: 300 1.7vmax cursive;
    }

    .resetPasswordForm>div>svg {
        font-size: 2.8vmax;
    }

    .resetPasswordBtn {
        font: 300 1.9vmax "Roboto";
        padding: 1.8vmax;
    }
}